import { useState, useEffect } from 'react';

import Fade from '@material-ui/core/Fade'

import NoobFilter from "../components/NoobFilter"
import NoobTable from "../components/NoobTable"
import { blogs } from '../asset/blogs'

function Blogs(props) {
  const { setTitle, setFooterAppear } = props
  const [rows, setRows] = useState([])

  useEffect(() => {
    setTitle("Blog")
    setRows(blogs)
  }, [setTitle, setRows])

  return (
    <Fade in={true} timeout={1000} onEntered={() => setFooterAppear(true)}>
      <div>
        <NoobFilter rows={rows} setRows={setRows} data={blogs}/>
        <br />
        <NoobTable rows={rows} rowsPerPage={10} link={"#/blogs"} />
      </div>
    </Fade>
  )
}

export default Blogs